.services__list {
    margin-top: 48px;

    @media screen and (max-width: 1199px) {
        // width: 86%;
    }

    @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
    }

    .services__item+.services__item {
        @media screen and (max-width: 767px) {
            margin-top: 42px;
        }
    }

    .services__item-title {
        font-size: 24px;

        @media screen and (min-width: 768px) {
            text-align: center;
        }
    }
}

.services__steps-list {
    margin-top: 36px;
    display: flex;
    flex-direction: column;

    @media screen and (min-width: 768px) {
        align-items: center;
    }

}

.services__steps-item {
    position: relative;
    transition: transform 250ms ease-in-out;

    &+& {
        margin-top: 24px;
    }

    @media screen and (max-width: 767px) {
        &:nth-child(1) {
            margin-right: 24%;
        }

        &:nth-child(2) {
            margin-right: 18%;
        }

        &:nth-child(3) {
            margin-right: 12%;
        }

        &:nth-child(4) {
            margin-right: 6%;
        }
    }

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        width: 86%;
    }

    @media screen and (min-width: 1200px) {
        width: 70%;
    }

    .services__steps-item-cont {
        position: relative;
        z-index: 1;
        border: 1px solid $main-color;
        border-radius: 22px;
        background-color: $secondary-background-color;
        font-size: 18px;

        @media screen and (max-width: 1199px) {
            padding: 26px;
        }

        @media screen and (min-width: 1200px) {
            padding: 36px;
        }
    }

    &:hover {

        @media screen and (max-width: 767px) {
            transform: translatey(2px) scale(1.02);
        }

        @media screen and (min-width: 768px) {
            transform: translatey(12px) scale(1.02);
        }

        &::after {
            content: "";
            background: $secondary-background-color;
            filter: blur(15px) brightness(1);
            position: absolute;
            border-radius: 22px;
            width: 108%;
            height: 108%;
            left: -2%;
            top: -2%;
            z-index: 0;
        }
    }
}